.projects-section {
  background-color: #000;
  padding: 80px 20px;
  color: white;
}

.section-heading {
  text-align: center;
  font-size: 3rem;
  font-weight: bold;
  margin-bottom: 40px;
}

.projects-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  gap: 30px;
  max-width: 1200px;
  margin: auto;
}

.project-card {
  background-color: #1a1a2e;
  border-radius: 12px;
  padding: 20px;
  box-shadow: 0 0 10px rgba(255, 255, 255, 0.05);
  transition: transform 0.3s ease;
}

.project-card:hover {
  transform: translateY(-5px);
}

.project-image {
  width: 100%;
  height: 180px;
  object-fit: cover;
  border-radius: 8px;
  margin-bottom: 15px;
}

.project-title {
  font-size: 1.4rem;
  font-weight: bold;
  color: #ffc300;
}

.project-description {
  margin: 10px 0;
  color: #ccc;
}

.project-achievement {
  margin: 10px 0 12px;
  padding: 10px 12px;
  border-radius: 10px;
  background: rgba(255, 195, 0, 0.12);
  border: 1px solid rgba(255, 195, 0, 0.35);
  color: #ffc300;
  font-weight: 700;
  font-size: 0.98rem;
  display: inline-flex;
  align-items: center;
  gap: 8px;
}

.project-tech {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  margin-bottom: 12px;
}

.tech-badge {
  background-color: #ffc300;
  color: #000;
  padding: 4px 10px;
  border-radius: 20px;
  font-size: 0.8rem;
  font-weight: 600;
}

.project-links a {
  color: #fff;
  margin-right: 15px;
  text-decoration: underline;
  font-weight: 500;

}
@media (max-width: 768px) {
  .projects-grid {
    grid-template-columns: 1fr;
    padding: 0 20px;
  }

  .project-card {
    padding: 16px;
  }

  .project-title {
    font-size: 1.2rem;
  }

  .project-description {
    font-size: 1rem;
  }
}

